a, href {
	text-decoration: none;
	font-weight: 800;
	border-bottom: 0;
	color: #8e8e8e;
	overflow: hidden;
	background: linear-gradient(to right, #424242, #424242 50%, #8e8e8e 50%);
	background-clip: text;
	-webkit-background-clip: text;
	-webkit-text-fill-color: transparent;
	background-size: 200% 100%;
	background-position: 100%;
	transition: background-position 200ms ease;
}

a:hover, href:hover {
	background-position: 0 100%;
	border-bottom: 0;
	color: #696969;
   text-decoration: none;
}

::-moz-selection {
  color: #444444;
  background: #ffdc8a;
}

::selection {
  color: #444444;
  background: #ffdc8a;
}

